import React from "react";
import "./SobrePage.css"; // Arquivo CSS para estilizao

const SobrePage = () => {
  return (
    <div className="sobre-container">
      <div className="sobre-content"> {/* Adicionado para criar a rea branca */}
        <h2>Sobre o Coletivo Kalnia</h2>
        <p>
          HISTRICO  
          Em 2017 um grupo de amigos se reunia na cidade de Atibaia, interior de So Paulo para fundar um coletivo literrio com um nome bem sugestivo: Kalnia. A escolha se deu, como protesto s normatizaes e aos esquemas sempre redondinhos nos quais a literatura deve se equilibrar. E no! Definitivamente no somos caluniadores. Nem com c e nem com K. Somos sim, palavras caminhantes que se afinam com um mundo em constante mudana. 
Nosso objetivo  caminhar ao encontro das boas histrias, dos poemas escondidos, perdidos e sem chance. Encontramos na literatura, uma espcie de alento para as dores do mundo, mas tambm  atravs dela que denunciamos as mazelas da atualidade. Por ela e com ela sorrimos, choramos, saboreamos as contradies to presentes na vida das pessoas.
Por isso, somos um grupo ligado aos movimentos da nossa poca, na tentativa de estabelecer conexes profundas com a realidade e, principalmente com a arte em seu processo de ebulio.
Somos a favor da vida e do respeito a tudo que se relaciona a esta. Nossas propostas estabelecem-se pela via democrtica em consonncia com a emancipao humana.
A arte nos transborda, nos move, e, sobretudo alimenta nossas esperanas. Clamamos pela sociedade leitora. Confiamos no futuro e no apelo da palavra como uma grande alavanca na transformao do planeta.
          [...]
          <br /><br />
          Juliana Gobbe  
          Coordenadora  
          Coletivo Literrio Kalnia  
        </p>
      </div>
    </div>
  );
};

export default SobrePage;